import {
  Component,
  computed,
  DestroyRef,
  inject,
  OnInit,
  signal,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { ActivatedRoute, Data } from '@angular/router';
import {
  catchError,
  combineLatest,
  EMPTY,
  from,
  of,
  switchMap,
  tap,
  throwError,
} from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

// Third-party imports
import { AccordionModule } from 'primeng/accordion';
import { DividerModule } from 'primeng/divider';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

// Application imports
import {
  AccountsService,
  VendorCreateRequest,
  VendorResponse,
} from '../../../../../../api';
import { VendorDataStrategy } from '../../../../strategies';
import { AdminDetailSection } from '../../../../models';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../../shared/utils';
import { AppFeatures, DEFAULT_SYSTEM_ID } from '../../../../../../core/models';
import { SectionConfigBuilder } from '../../../../factories';
import { BroadcastEventMessageService } from '../../../../services/broadcast-event-messages.service';
import { FeedSectionComponent } from '../../../shared/feed-section/feed-section.component';
import { AuthDataService } from '../../../../../../core/services/auth-data.service';
import { EntityOperationRegistryService } from '../../../../services/entity/entity-operation-registry.service';
import { FeatureSectionComponent } from '../../../shared/feature-section/feature-section.component';

interface VendorRouteData extends Data {
  vendor?: VendorResponse;
}

interface EntityUpdatedMessage {
  entityType: string;
  data: VendorResponse;
}

@Component({
  selector: 'app-vendor-details-container',
  imports: [
    AccordionModule,
    DividerModule,
    ProgressSpinnerModule,
    CommonModule,
  ],
  templateUrl: './vendor-details-container.component.html',
  styleUrl: './vendor-details-container.component.scss',
})
export class VendorDetailsContainerComponent implements OnInit {
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly authDataService = inject(AuthDataService);
  private readonly operationRegistry = inject(EntityOperationRegistryService);
  private readonly accountsService = inject(AccountsService);
  protected informationSections = signal<AdminDetailSection[]>([]);
  protected settingsSections = signal<AdminDetailSection[]>([]);
  protected featuresSections = signal<AdminDetailSection[]>([]);
  protected limitsSections = signal<AdminDetailSection[]>([]);
  protected statisticsSections = signal<AdminDetailSection[]>([]);
  protected changesSections = signal<AdminDetailSection[]>([]);
  protected loading = signal(true);
  protected entity = signal<VendorResponse | null>(null);
  protected error = signal<string | null>(null);

  // Helper computed properties
  protected hasInformationSections = computed(
    () => this.informationSections().length > 0,
  );
  protected hasSettingsSections = computed(
    () => this.settingsSections().length > 0,
  );
  protected hasFeaturesSections = computed(
    () => this.featuresSections().length > 0,
  );
  protected hasLimitsSections = computed(
    () => this.limitsSections().length > 0,
  );
  protected hasStatisticsSections = computed(
    () => this.statisticsSections().length > 0,
  );
  protected hasChangesSections = computed(
    () => this.changesSections().length > 0,
  );

  // Services
  private readonly dataStrategy = inject(VendorDataStrategy);
  private readonly destroyRef = inject(DestroyRef);
  private readonly route = inject(ActivatedRoute);

  constructor() {
    this.operationRegistry.registerEntityOperations('vendor', {
      update: (id, data) => this.accountsService.updateVendor(id, data),
      create: (data: VendorCreateRequest) =>
        this.accountsService.createVendor(data),
      get: (id) => this.accountsService.getVendor(id),
    });
  }

  private isEntityUpdatedMessage(
    message: unknown,
  ): message is EntityUpdatedMessage {
    return (
      isDefined(message) &&
      message !== null &&
      typeof message === 'object' &&
      'entityType' in message &&
      'data' in message &&
      (message as EntityUpdatedMessage).entityType === 'vendor'
    );
  }
  ngOnInit(): void {
    this.initializeEntityData();
    this.broadcastService
      .on('entity-updated')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((message) => {
        if (this.isEntityUpdatedMessage(message)) {
          this.entity.set(message.data);
          this.updateSectionsWithEntityData();
        }
      });
  }

  private initializeEntityData(): void {
    combineLatest([
      this.route.paramMap,
      this.route.parent?.parent?.data ?? of<VendorRouteData>({}),
    ])
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        tap(() => {
          this.loading.set(true);
          this.setupSectionStructure();
        }),
        switchMap(([params, data]) => {
          const vendorId = params.get('vendorId') ?? data['vendor']?.id;

          if (isDefined(vendorId)) {
            return from(this.dataStrategy.fetchById(vendorId));
          }

          if (!isEmpty(data['vendor'])) {
            return of(data['vendor']);
          }

          return throwError(() => new Error('Vendor ID not found'));
        }),
        catchError(() => {
          this.error.set('Failed to load vendor data');
          return EMPTY;
        }),
        tap((vendorData) => {
          this.entity.set(vendorData);
          this.loading.set(false);
          this.updateSectionsWithEntityData();
        }),
      )
      .subscribe();
  }

  private setupSectionStructure(): void {
    // Register information sections
    this.informationSections.set(
      SectionConfigBuilder.createVendorInformationSections(null),
    );

    // Register settings sections
    const isSystemUser =
      this.authDataService.getUserProfile()?.owner?.id === DEFAULT_SYSTEM_ID;
    this.settingsSections.set(
      SectionConfigBuilder.createVendorSettingsSections(null),
    );

    if (isSystemUser) {
      this.limitsSections.set(
        SectionConfigBuilder.createVendorLimitsSections(null),
      );
    }

    if (this.authDataService.getUserProfile()?.owner?.type !== 'client') {
      // Register app features sections
      const appFeatures = ['page-moreapps']; // Just moreapps feature

      const allFeatureSections = appFeatures.flatMap((feature) =>
        SectionConfigBuilder.forVendor(null)
          .addSection(feature, 'Features', FeatureSectionComponent)
          .build(),
      );
      this.featuresSections.set(allFeatureSections);
    }
  }

  private updateSectionsWithEntityData(): void {
    const entityData = this.entity();
    if (isNullOrUndefined(entityData)) return;

    // Update sections with entity data
    this.informationSections.set(
      SectionConfigBuilder.createVendorInformationSections(entityData),
    );

    const isSystemUser =
      this.authDataService.getUserProfile()?.owner?.id === DEFAULT_SYSTEM_ID;
    this.settingsSections.set(
      SectionConfigBuilder.createVendorSettingsSections(entityData),
    );

    if (isSystemUser) {
      this.limitsSections.set(
        SectionConfigBuilder.createVendorLimitsSections(entityData),
      );
    }

    if (this.authDataService.getUserProfile()?.owner?.type !== 'client') {
      const appFeatures = ['page-moreapps'];

      const allFeatureSections = appFeatures.flatMap((feature) =>
        SectionConfigBuilder.forVendor(entityData)
          .addSection(
            feature,
            'App Features',
            FeatureSectionComponent<VendorResponse>,
            { category: 'features' },
            {
              feature: AppFeatures[feature],
              entityType: 'vendor',
            },
          )
          .build(),
      );

      this.featuresSections.set(allFeatureSections);
    }

    this.statisticsSections.set(
      SectionConfigBuilder.createVendorStatisticsSections(entityData),
    );

    this.changesSections.set([
      {
        id: 'vendor-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: entityData,
          entityType: 'vendor',
        },
        category: 'feed',
        order: 10,
      } as AdminDetailSection,
    ]);
  }

  // Method for child components to save data
  async updateEntity(updates: Partial<VendorResponse>): Promise<void> {
    const vendor = this.entity();
    if (isNullOrUndefined(vendor)) return;

    try {
      this.loading.set(true);
      const updatedEntity = await this.dataStrategy.update(vendor.id, updates);
      this.entity.set(updatedEntity);
    } catch (error) {
      this.error.set(
        `${error instanceof Error ? error.message : String(error)}`,
      );
    } finally {
      this.loading.set(false);
    }
  }

  // Helper method to combine inputs
  protected getSectionInputs(
    section: AdminDetailSection,
  ): Record<string, unknown> {
    return {
      ...section.componentInputs,
    };
  }
}
